.home-page {
  display: flex;
  flex-direction: column;
}
  
.content-wrapper {
  display: flex;
  flex-direction: column;
  padding-top: var(--top-padding);
  padding-bottom: 240px;
  row-gap: 240px;
}

.section-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: var(--content-gap-desktop);
}

.text-section {
  display: flex;
  flex-direction: column;
  row-gap: var(--para-gap-desktop);
}

.banner {
  height: calc(100vh - var(--top-padding));
  min-height: 760px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  row-gap: 1.5rem;
  padding-bottom: 1.5rem;
}

.banner-space {
  display: flex;
  justify-content: space-between; /* Elements at opposite ends with space in between */
  align-items: flex-end;
  width: 100%; /* Adjust the width of the container as needed */
}

.banner-image {
  object-fit: cover;
  border-radius: 2rem;
  width: 100%; /* Adjust the width of the container as needed */
  height: 100%;
  min-height: 640px;
}

.innovators {
  display: flex;
  gap: var(--content-gap-desktop);
  align-items: center;
}

.innovators-image {
  width: 100%;
  object-fit: contain;
  background-color: var(--white);
  border-radius: 2rem;
}

.innovators-text {
  width: 100%;
  display: flex;
  flex-direction: column;
  row-gap: 40px;
  align-items: center;
}

.card-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 24px;
}

.grid-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: var(--content-gap-mobile);
  width: 100%;
}

.link-view-more {
  align-self: center;
}

.site-button {
  padding: 16px 64px;
  border-radius: 100px;
  cursor: pointer;
}

.outline {
  border: 4px solid var(--body);
  padding-top: 12px;
  padding-bottom: 12px;
}

.disabled {
  border: none;
  cursor: default;
}

.disabled p {
  color: var(--on-disabled);
}

@media (max-width: 1200px) {
  .scroll-more {
      display: none;
  }
  .section-wrapper {
    row-gap: var(--content-gap-tablet);
  }
  .text-section {
    row-gap: var(--para-gap-tablet);
  }
  .banner {
    height: auto;
    min-height: unset;
  }
  .content-wrapper {
    row-gap: 180px;
    padding-bottom: 180px;
  }
  .innovators {
    flex-direction: column;
    gap: var(--content-gap-tablet);
  }
  .innovators-image {
    aspect-ratio: 2;
  }
}

@media (max-width: 640px) {
  .content-wrapper {
      padding-top: var(--top-padding-mobile);
  }
  .section-wrapper {
    row-gap: var(--content-gap-mobile);
  }
  .text-section {
    row-gap: var(--para-gap-mobile);
  }
  .content-wrapper {
    row-gap: 90px;
    padding-bottom: 90px;
  }
  .banner-image {
    min-height: 320px;
  }
  .innovators {
    gap: var(--content-gap-mobile);
  }
  .innovators-image {
    aspect-ratio: unset;
  }
}